import React from "react";

const Tags = (props) => {
  const languages = props.languages;
  const tools = props.tools;
  let allTags = [];
  allTags.push(props.role);
  allTags.push(props.level);
  languages.forEach((lang) => allTags.push(lang));
  tools.forEach((tool) => allTags.push(tool));

  let i = 101;
  const handleClick = (e) => {
    props.onClick(e.target.textContent);
  };
  const tagEl = allTags.map((tag) => {
    return (
      <div className="tag" key={i++} onClick={handleClick}>
        {tag}
      </div>
    );
  });

  return (
    <div className="tags" key={i}>
      {tagEl}
    </div>
  );
};

export default Tags;
