import React from "react";
import Tags from "./Tags";

export default function Card(props) {
  const data = props.data;
  const style = {
    borderLeft: data.featured ? "0.35rem solid hsl(180, 29%, 50%)" : "none",
  };
  return (
    <div className="card" style={style}>
      <img src={data.logo} className="company-logo" alt="company-logo" />
      <div className="company-info">
        <div className="top">
          <p className="company-name">{data.company}</p>
          {data.new && <div className="new">new!</div>}
          {data.featured && <div className="featured">featured</div>}
        </div>
        <p className="role">{data.position}</p>
        <div className="extra-info">
          <span>{data.postedAt}</span>
          <span>&bull;</span>
          <span>{data.contract}</span>
          <span>&bull;</span>
          <span>{data.location}</span>
        </div>
      </div>
      <Tags
        languages={data.languages}
        tools={data.tools}
        role={data.role}
        level={data.level}
        onClick={props.onClick}
      />
    </div>
  );
}
