import React from "react";
import Header from "./components/Header";
import JobCards from "./components/JobCards";
import Footer from "./components/Footer";

const App = () => {
  const [filter, setFilter] = React.useState([]);

  function arrayRemove(arr, value) {
    return arr.filter(function (ele) {
      return ele !== value;
    });
  }

  const onClick = (newVal, x) => {
    if (newVal === "clear") {
      setFilter([]);
    } else if (!filter.includes(newVal)) {
      setFilter((prevFilter) => [...prevFilter, newVal]);
    } else if (x && x === "x") {
      setFilter((prevFilter) => arrayRemove(prevFilter, newVal));
    }
  };
  return (
    <div>
      <Header filter={filter} setFilter={setFilter} onClick={onClick} />
      <JobCards filter={filter} setFilter={setFilter} onClick={onClick} />
      <Footer />
    </div>
  );
};

export default App;
