import React from "react";
import Card from "./Card";
import customData from "../data";

const JobCards = (props) => {
  if (props.filter.length === 0) {
    const cardEl = customData.map((data) => {
      return <Card data={data} onClick={props.onClick} key={data.id} />;
    });
    return cardEl;
  } else {
    const cardEl = customData.map((data) => {
      const languages = data.languages;
      const tools = data.tools;
      let allTags = [];
      allTags.push(data.role);
      allTags.push(data.level);
      languages.forEach((lang) => allTags.push(lang));
      tools.forEach((tool) => allTags.push(tool));
      let flag = 0;
      allTags.forEach((tag) => {
        if (props.filter.includes(tag)) flag++;
      });
      if (flag === props.filter.length) {
        return <Card data={data} onClick={props.onClick} key={data.id} />;
      } else return null;
    });
    return cardEl;
  }
};

export default JobCards;
