import React from "react";

const Filter = (props) => {
  const handleClick = (e) => {
    if (e.target.textContent !== "clear") {
      props.onClick(e.target.previousElementSibling.textContent, "x");
    } else props.onClick(e.target.textContent);
  };
  let i = 201;
  const filterTagEl = props.filter.map((fl) => {
    return (
      <div className="filter-tag-el" key={i++}>
        <div>{fl}</div>
        <div className="close-btn" onClick={handleClick}>
          X
        </div>
      </div>
    );
  });
  return (
    <div className="filter">
      <div className="filter-tags">{filterTagEl}</div>
      <p onClick={handleClick} className="clear">
        clear
      </p>
    </div>
  );
};

export default Filter;
